<template>
	<div>
		<Loader v-if="loading"></Loader>

		<div v-else-if="record"> 
			<div class="breadcrumb-wrap">
				<router-link to="/history" class="breadcrumb">История</router-link>
				<a class="breadcrumb" v-if="record.type === 'outcome'">
					Расход
				</a>
				<a class="breadcrumb" v-else>
					Доход
				</a>
			</div>
			<div class="row">
				<div class="col s12 m6">
					<div class="card" :class="{
						'red': record.type === 'outcome',
						'green': record.type === 'income',
						}">
						<div class="card-content white-text">
							<p>Описание: {{record.description}}</p>
							<p>Сумма: {{record.amount | currencyFilter}}</p>
							<p>Категория: {{record.categoryName}}</p>

							<small>{{record.date | dateFilter}}</small>
						</div>
					</div>
				</div>
			</div>
		</div>

		<p v-else>Записи с id={{this.$route.params.id}} не найдена</p>
	</div>
</template>

<script>
	export default {
		name: 'detailRecord',
		data(){
			return{
				loading: true,
				record: null
			}
		},
		async mounted(){
			const id = this.$route.params.id //получение id текущей записи из адресной строки
			const record = await this.$store.dispatch('fetchRecordsById', id)
			const category = await this.$store.dispatch('getCategoryById', record.categoryId)

			console.log(record )
			this.record = {
				...record,
				categoryName: category.title
			}
			this.loading = false
		}
	}
</script>